<template>
  <div>
    <p>會員名稱</p>
    <p>{{ bean.name }}&nbsp;</p>
    <p>Email</p>
    <p>{{ bean.email }}&nbsp;</p>
    <p>組資單位</p>
    <p>{{ bean.company }}&nbsp;</p>
    <p>聯絡電話</p>
    <p>{{ bean.phone }}&nbsp;</p>
    <p>聯絡地址</p>
    <p>{{ bean.address }}&nbsp;</p>
    <p>生日</p>
    <p>{{ bean.birthday }}&nbsp;</p>
    <p>積分</p>
    <p>{{ bean.integral }}&nbsp;</p>
    <p><el-button type="primary" route="/member">返回上頁</el-button></p>
  </div>
</template>

<script>
import $ from "jquery";
export default {
  name: "memberDetail",
  components: {},
  data() {
    return {
      bean: {},
    };
  },
  created() {
    const url = new URL(location.href);
     console.log(url.searchParams.keys());
    const id = url.searchParams.get("id");
    console.log(id);
    $.ajax({
      url: "/topic/backstage/detail/" + id,
      type: "POST",
      async: false, //同步請求
      cache: false, //不快取頁面
      success: (response) => {
        this.bean = response;

        console.log(response);
      },
      error: function (returndata) {
        console.log(returndata);
      },
    });
  },
};
</script>

